@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@200;300;400;600;700;800;900&display=swap");

$primary: #257cff;
$white: #fff;
$dark: #313131;
$darker: #1b1b1b;
$darkest: #0f0f0f;

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Montserrat";
}

header.NavBar {
  z-index: 5;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: $primary;
  // position: sticky;
  top: 0;
  & > div {
    display: flex;
    align-items: center;
    justify-content: center;
    // gap: 20px;
    h1 {
      font-size: 1.5rem;
      color: $white;
    }
    svg {
      color: $white;
      cursor: pointer;
      padding: 16px;
      height: 60px;
      width: 60px;
    }
  }
}

section.edit {
  display: flex;
  // height: 100vh;
  overflow: hidden;
}

.SidePane {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
  background-color: $darker;
  padding-top: 20px;
  overflow: hidden;
  align-items: flex-start;
  align-items: stretch;
  transition: 0.5s;
  &.closed {
    width: 64px;
  }
  &.open {
    width: 175px;
  }
  h4 {
    padding: 20px 20px 0 20px;
    color: $dark;
    letter-spacing: 3px;
    // opacity: 0.5;
  }
  .files {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    .File {
      padding: 20px 25px 20px 20px;
      display: flex;
      align-items: center;
      gap: 20px;
      background-color: $darker;
      transition: 0.2s;
      cursor: pointer;
      &:hover,
      &.active {
        background-color: $dark;
      }
      p {
        color: $white;
        width: 100px;
      }
    }
  }
}

.workspace {
  flex: 1;
  display: flex;
  align-items: stretch;
  justify-content: stretch;
  height: 100vh;
  & > div {
    width: 50%;
    flex: 1;
    padding-bottom: 50px;
  }
  &.vertical {
    & > div {
      width: 100%;
      height: 100vh;
      // flex: 1;
      overflow: hidden;
    }
    flex-direction: column;
  }
}

.Editor {
  background-color: #1e1e1e;
  .head {
    padding: 10px;
    display: flex;
    align-items: center;
    gap: 10px;
    p {
      color: $white;
      opacity: 50%;
    }
  }
}

.Output {
  background-color: $white;
  // padding-top: 10px;
  iframe {
    height: 100%;
    width: 100%;
    border: none;
  }
}
